code,
pre {
    background-color: "#282C34";
    color: whitesmoke;
}

code,
code span {
    font-family: monospace;
    font-weight: 400;
    font-size: 16px;
}

code {
    background-color: "#282C34";
    color: whitesmoke;

    &::selection,
    & *::selection {
        background: $old-lavender;
        color: whitesmoke;
    }

    &:not(pre code) {
        background-color: #272822;
        border-radius: 5px;
        padding: 3.5px;
    }
}

pre {
    background-color: "#282C34";
    color: whitesmoke;

    filter: drop-shadow(0 0 15px #282c34);
}
